import { GET_VGAMES_BY_NAME } from '.';
import axios from 'axios'

export default function getvgbyname(name) {
    return async function (dispatch){ 
        try { const result = await axios.get(`http://localhost:3001/videogames?name=${name}`);
            return dispatch({ type: GET_VGAMES_BY_NAME, payload: result.data })  
         } catch (error) {
            alert(`Juego ${name} no encontrado.`)
            window.location.replace("http://localhost:3000/videogames");
         }                                                                                                  
    }}
